import React, { Component } from "react";
import { Grid, Cell, List, ListItem, ListItemContent } from "react-mdl";

class Contact extends Component {
  render() {
    return (
      <div className="contact-body">
        <Grid className="contact-grid">
          <Cell col={6}>
            <h2 style={{ color: "grey" }}>Akash Vichare</h2>
            <img
              src="https://instagram.fewr1-1.fna.fbcdn.net/vp/47378f623b399f0ef030562be158975a/5E1FED63/t51.2885-19/s150x150/33983136_1992255680848917_5469890681113673728_n.jpg?_nc_ht=instagram.fewr1-1.fna.fbcdn.net"
              style={{ height: "200px", borderRadius: "50%" }}
            />
            <p
              style={{
                width: "75%",
                margin: "auto",
                paddingTop: "1em",
                color: "grey"
              }}
            >
              Feel free to contact me with any Front-End / UI / UX / FullStack
              Developer roles.
            </p>
            <p style={{ color: "grey" }}>
              {" "}
              To set up a time for us to speak, contact me via my e-mail or
              phone number.
            </p>
          </Cell>
          <Cell col={6}>
            <h2 style={{ color: "grey" }}>Contact Me</h2>
            <hr />
            <div className="contact-list">
              <List>
                <ListItem>
                  <ListItemContent
                    style={{
                      fontSize: "30px",
                      fontFamily: "Anton",
                      color: "grey"
                    }}
                  >
                    <i
                      style={{ color: "lightgreen" }}
                      class="fa fa-phone"
                      aria-hidden="true"
                    ></i>
                    (609) 756-6516
                  </ListItemContent>
                </ListItem>

                <ListItem>
                  <ListItemContent
                    style={{
                      fontSize: "30px",
                      fontFamily: "Anton",
                      color: "grey"
                    }}
                  >
                    <i
                      style={{ color: "black" }}
                      className="fa fa-envelope"
                      aria-hidden="true"
                    />
                    vichareakash92@gmail.com
                  </ListItemContent>
                </ListItem>
                <ListItem>
                  <ListItemContent
                    style={{
                      fontSize: "30px",
                      fontFamily: "Anton",
                      color: "grey"
                    }}
                  >
                    <i
                      style={{ color: "#00aff0" }}
                      className="fa fa-skype"
                      aria-hidden="true"
                    />
                    akashvichare01
                  </ListItemContent>
                </ListItem>
              </List>
            </div>
          </Cell>
        </Grid>
      </div>
    );
  }
}
export default Contact;
