import React, { Component } from "react";
import { Grid, Cell } from "react-mdl";

class About extends Component {
  render() {
    return (
      <div>
        <Grid>
          <Cell col={8}>
            <h2 style={{ color: "Black" }}>Work Experience</h2>
            <h4 style={{ color: "Blue" }}>Integra Technologies LLC</h4>
            <hr style={{ borderTop: "3px solid #833fb2", width: "100%" }} />
            <p style={{ color: "grey" }}>
              * Developing, optimizing and maintaining the websites and
              applications utilizing HTML, CSS, JS, jQuery and various APIs to
              meet the goals.{" "}
            </p>
            <p style={{ color: "grey" }}>
              * Built user interactive web components for single page
              applications using ES6, React.js, Redux, jQuery along with Gulp,
              Grunt, Webpack, Babel and Git.
            </p>
            <p style={{ color: "grey" }}>
              * Designed and developed dynamic and interactive websites and
              applications that ensured high traffic, page views and better user
              experience.
            </p>
            <p style={{ color: "grey" }}>
              * Fixed front end bugs from existing websites and implemented
              enhancements. (HTML, CSS, JavaScript errors, Jasmine, Mocha
              testing (A/B split testing), unit testing.)
            </p>
            <p style={{ color: "grey" }}>
              * Evaluate code to ensure that it is valid, properly structured,
              meets industry standards and is compatible with browsers, devices
              or operating systems.
            </p>

            <h4 style={{ color: "Blue" }}>Praxis Interactive Services</h4>
            <hr style={{ borderTop: "3px solid #833fb2", width: "100%" }} />
            <p style={{ color: "grey" }}>
              * Maintain websites and applications, including content updates,
              account administration, debugging, feature enhancements and
              documentations, testing and regular updates.
            </p>
            <p style={{ color: "grey" }}>
              * Collaborated with senior level developers to develop projects
              for the application throughout various phases of the project
              lifecycle.
            </p>
            <p style={{ color: "grey" }}>
              * Managed client websites to keep them up to date, fix bugs and
              install updates as required
            </p>
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default About;
