import React, { Component } from "react";
import { Grid, Cell } from "react-mdl";

class Landing extends Component {
  render() {
    return (
      <div style={{ width: "100%", margin: "auto" }}>
        <Grid className="landing-grid">
          <Cell col={12}>
            <div className="banner-text">
              <img src="https://instagram.fewr1-1.fna.fbcdn.net/vp/47378f623b399f0ef030562be158975a/5E1FED63/t51.2885-19/s150x150/33983136_1992255680848917_5469890681113673728_n.jpg?_nc_ht=instagram.fewr1-1.fna.fbcdn.net" />

              <h1>Akash Vichare</h1>
              <h2>Web Developer</h2>

              <hr style={{ border: "5px solid white", borderRadius: "5px" }} />

              <p>
                JavaScript | HTML/CSS | React | React Native | Angular | NodeJS
                | Express | MongoDB | Web Services
              </p>

              <div className="social-links">
                {/* LinkedIn */}
                <a
                  href="https://www.linkedin.com/in/akash-vichare-64549b128/"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i
                    style={{ color: "#0E76A8" }}
                    className="fa fa-linkedin-square"
                    aria-hidden="true"
                  />
                </a>

                {/* Instagram */}
                <a
                  href="https://www.instagram.com/akashvichare01/"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i
                    style={{ color: "orange" }}
                    className="fa fa-instagram"
                    aria-hidden="true"
                  />
                </a>

                {/* Facebook */}
                <a
                  href="https://www.facebook.com/akash.vichare.7"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i
                    style={{ color: "#3B5998" }}
                    className="fa fa-facebook-square"
                    aria-hidden="true"
                  />
                </a>
              </div>
            </div>
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default Landing;
