import React, { Component } from "react";
import { Grid, Cell } from "react-mdl";

class About extends Component {
  render() {
    return (
      <div>
        <Grid>
          <Cell col={8}>
            <h2 style={{ color: "Black" }}>Projects</h2>
            <h4 style={{ color: "blue" }}>
              Chat Application using Angular 5/6
            </h4>
            <hr style={{ borderTop: "3px solid #833fb2", width: "100%" }} />
            <p style={{ color: "grey" }}>
              * Created a single page application for a chat interface using
              Angular 4 framework.{" "}
            </p>
            <p style={{ color: "grey" }}>
              * Set up the user authentication, wrote an authentication service
              and managed user objectives in a live database.
            </p>
            <p style={{ color: "grey" }}>
              * Responsive Design and SASS/CSS for styling
            </p>

            <h4 style={{ color: "blue" }}>
              Expense Management Application using React
            </h4>
            <hr style={{ borderTop: "3px solid #833fb2", width: "100%" }} />
            <p style={{ color: "grey" }}>
              * Developed an Expense Management application using React
              framework
            </p>
            <p style={{ color: "grey" }}>
              * Defined various React components and Implemented features like
              user authentication, form validation and database storage
            </p>
            <p style={{ color: "grey" }}>
              * Integrated Firebase with the App using Redux and set up the
              routes using React Router.
            </p>
            <h4 style={{ color: "Blue" }}>
              Blog portfolio for camping experiences
            </h4>
            <hr style={{ borderTop: "3px solid #833fb2", width: "100%" }} />
            <p style={{ color: "grey" }}>
              * Created a blog website to help the users to share their camping
              experiences using pictures and comments.
            </p>
            <p style={{ color: "grey" }}>
              * Implemented features like Authentication and Association using
              APIs like ejs, passport, express, mongoose.
            </p>
            <p style={{ color: "grey" }}>
              * Created a responsive design using Node.js, Bootstrap, CSS and
              Express.js
            </p>
          </Cell>
        </Grid>
      </div>
    );
  }
}

export default About;
